<template>
	<div class="body">
		<!--分配模块-->
		<div class="cash_block" v-if="cash_type == 1">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">关联回款</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table ref="multipleTable" :data="tableData1" border style="width: 100%">
						<el-table-column prop="coin" label="序号">
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column prop="fund_id_name" label="标的类别">
						</el-table-column>
						<el-table-column prop="fund_name" label="标的名称" width="120">
						</el-table-column>
						<el-table-column prop="back_type_name" label="回款类型">
						</el-table-column>
						<el-table-column prop="back" label="回款本金（万元）">
						</el-table-column>
						<el-table-column prop="profile" label="回款收益-税后（万元）">
						</el-table-column>
					</el-table>
				</div>
			</div>
		</div>
		<div class="cash_block" v-if="cash_type == 1">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">分配</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table ref="multipleTable" :data="tableData2" border style="width: 100%">
						<el-table-column prop="event" label="事件">
						</el-table-column>
						<el-table-column prop="fund_type_name" label="认购基金类别" width="120">
						</el-table-column>
						<el-table-column prop="receive_date" label="到账时间">
						</el-table-column>
						<el-table-column prop="sum_back" label="分配本金">
						</el-table-column>
						<el-table-column prop="sum_profile" label="分配收益-税后（万元）">
						</el-table-column>
						<el-table-column prop="sum_calculate" label="分配总额（万元）">
						</el-table-column>
						<el-table-column prop="unit" label="币种">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		
		<!--缴款模块-->
		<div class="cash_block" v-if="cash_type == 2">
			<div class="cash_block_title flex_start">
				<span class="circle_blue"></span> 
				<span class="name">缴款</span>
			</div>
			<div class="cash_block_main">
				<div class="cash_flow_table table">
					<el-table ref="multipleTable" :data="tableData3" border style="width: 100%">
						<el-table-column prop="event" label="事件">
						</el-table-column>
						<el-table-column prop="fund_type_name" label="缴款基金类别">
						</el-table-column>
						<el-table-column prop="fund_name" label="缴款基金名称">
						</el-table-column>
						<el-table-column prop="should_subscribe" label="当期应缴金额（万元）">
						</el-table-column>
						<el-table-column prop="subscribe" label="当期实缴金额（万元）">
						</el-table-column>
						<el-table-column prop="invest_date" label="出资日期">
						</el-table-column>
						<el-table-column prop="unit" label="币种">
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
					</el-table>
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getDistributeSubFundCashListApi,getPaySubFundCashListApi } from '../../config/api'
	
	export default {
		name: 'subFundRunCashListDetail',
		data() {
			return {
				id: '',
				count: '',
				type: '',//列表中的inside_type字段的值，标示是否为母基金还是自管基金
				cash_type: '',//1代表分配；2代表缴款；
				tableData1: [],//分配
				tableData2: [],     
				tableData3: [],//缴款
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.count = this.$route.query.count;
			this.type = this.$route.query.type;
			this.cash_type = this.$route.query.cash_type;
			if (this.cash_type == 1) {
				this.getDistributeSubFundCashList()
			} else{
				this.getPaySubFundCashList()
			}
		},
		mounted() {},
		components: {},
		methods: {
			getDistributeSubFundCashList(){
				getDistributeSubFundCashListApi({
					childfund_id: this.id,
					count: this.count,
					type: this.type
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData1 = res.data.payload.data.detail;
						this.tableData2.push(res.data.payload.data.event);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getPaySubFundCashList(){
				getPaySubFundCashListApi({
					childfund_id: this.id,
					count: this.count,
					type: this.type
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData3.push(res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		},
	}
</script>

<style scoped="scoped">
	.cash_block_title{
		margin: 22px 0;
	}
	.cash_block_title .name{
		color: #6292F4;
		font-size: 16px;
	}
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
</style>


